<template>
  <div class="home">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div v-if="patientData">
      <div class="text-center >text-danger my-2" v-if="isLoading">
        <b-spinner class="align-middle"></b-spinner>
      </div>
      <div v-if="!isLoading && patientData.visits.length == 1" >
      </div>
      
      <div class="row"  v-if="!isLoading">
        <div class="col-lg-9 col-md-6 col-sm-6 mt-4">
          <div dir="ltr" v-if="!isLoading">
          <h5><b>{{visitOperationDic[$route.params.id].date + " - " + visitOperationDic[$route.params.id].time + " visit"}}</b></h5>
          <h6><b>Name: </b>{{patientData.name}} / <b>Age: </b>{{patientData.age}}  / <b>Phone: </b>{{patientData.phone}}</h6>
          <h6></h6>
        </div>
          <div class="row" style="background-color: #acd5d1; text-align:center;" v-if="!isLoading" dir="rtl" >
            <div class="col-lg-6 col-md-6 col-sm-6 mt-4">
              <h6>Upper Left</h6>
              <div class="row" v-if="!isLoading" style="padding-left:15px;align-items:flex-end;border-right:3px solid #9aadab; justify-content:flex-end ;border-bottom:1px solid #9aadab">

                <div v-for="(item, i) in teeth_up_left" :title="teethOperationDic[item.id] ? item.status : ''" v-if="patientData.age > 12 && leftSideTeethAdultCounter[i] || leftSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
                  <b-img class="teeth-img" 
                  :style="'background-color:' + (statusColor[teethOperationDic[item.id]] ? statusColor[teethOperationDic[item.id]] : '')"
                  :width="item.width" :src="item.imgSrc"></b-img>
                  <p style="padding-left:15px;">{{patientData.age > 12 ? leftSideTeethAdultCounter[i] : leftSideTeethChildCounter[i]}}</p>
                </div>
                
              </div>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-12 mt-4">
              <h6>Upper Right</h6>
              <div class="row" v-if="!isLoading" style="align-items:flex-end;border-right:3px solid #9aadab; border-bottom:1px solid #9aadab">

                <div v-for="(item, i) in teeth_up_right" :title="teethOperationDic[item.id] ? item.status : ''" v-if="patientData.age > 12 && rightSideTeethAdultCounter[i] || rightSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
                  <b-img class="teeth-img" 
                  :style="'background-color:' + (statusColor[teethOperationDic[item.id]] ? statusColor[teethOperationDic[item.id]] : '')"
                   :width="item.width" :src="item.imgSrc"></b-img>
                  <p style="padding-left:15px;">{{patientData.age > 12 ? rightSideTeethAdultCounter[i] : rightSideTeethChildCounter[i]}}</p>
                </div>

              </div>
            </div>
          </div>



          <div class="row" style="background-color: #acd5d1; text-align:center;" v-if="!isLoading" dir="rtl" >
            <div class="col-lg-6 col-md-6 col-sm-6">
              <div class="row" v-if="!isLoading" style="padding-left:20px;align-items:flex-start;border-right:3px solid #9aadab; justify-content:flex-end ;border-top:2px solid #9aadab">

                <div v-for="(item, i) in teeth_lower_left" :title="teethOperationDic[item.id] ? item.status : ''" v-if="patientData.age > 12 && leftSideTeethAdultCounter[i] || leftSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
                  <p style="padding-left:15px;">{{patientData.age > 12 ? leftSideTeethAdultCounter[i] : leftSideTeethChildCounter[i]}}</p>
                  <b-img class="teeth-img" 
                  :style="'background-color:' + (statusColor[teethOperationDic[item.id]] ? statusColor[teethOperationDic[item.id]] : '')"
                   style="transform: rotate(180deg);" :width="item.width" :src="item.imgSrc"></b-img>
                </div>
                <br>
              </div>
              <br>
              <h6>Lower Left</h6>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-12">
              <div class="row" v-if="!isLoading" style="align-items:flex-start;border-right:3px solid #9aadab; border-top:2px solid #9aadab">

                <div v-for="(item, i) in teeth_lower_right" :title="teethOperationDic[item.id] ? item.status : ''" v-if="patientData.age > 12 && rightSideTeethAdultCounter[i] || rightSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
                  <p style="padding-left:15px;">{{patientData.age > 12 ? rightSideTeethAdultCounter[i] : rightSideTeethChildCounter[i]}}</p>
                  <b-img class="teeth-img" 
                  :style="'background-color:' + (statusColor[teethOperationDic[item.id]] ? statusColor[teethOperationDic[item.id]] : '')"
                   style="transform: rotate(180deg);" :width="item.width" :src="item.imgSrc"></b-img>
                </div>

              </div>
              <br>
              <h6>Lower Right</h6>
            </div>
          </div>
        </div>


        <div class="col-lg-3 col-md-6 col-sm-6 mt-4" dir="ltr">
          <div dir="ltr" v-if="!isLoading">
            <h5><b>{{visitOperationDic[$route.params.id].date}}</b></h5>
            <h6>{{visitOperationDic[$route.params.id].operations.length + ' operations'}}</h6>
          </div>
          <!-- <p>{{visitOperationDic[$route.params.id].operations}}</p> -->
          <b-list-group v-if="visitOperationDic[$route.params.id].operations.length > 0">
            <b-list-group-item v-for="operation in visitOperationDic[$route.params.id].operations" :key="operation.id" class="flex-column align-items-start ">
              <div class="d-flex w-100 justify-content-between">
                <div>
                  <p class="mb-1">{{operation.operation}} - {{operation.cost + " EPG"}}</p>
                  <div>
                    <label style="font-size:12px">Price after discount</label>
                    <br>
                    <b-form-input
                      disabled
                      size="sm"
                      style="display:inline"
                      class="w-50"
                      id="operation-price"
                      name="operation-price"
                      v-model="operation.discoundCost"
                    ></b-form-input>
                  </div>
                                      
                </div>
              </div>
            </b-list-group-item>
          </b-list-group>
          <div v-else>
            <p><b-badge>No Operations.</b-badge></p>
          </div>
          <div v-if="!isLoading">
        </div>
        </div>
      </div>
    </div>
    


 </div>
</template>

<script>
  import { sendRequest }  from "../../utils/RequestHelper.js"
  import store from "../../store/index.js"

  export default {
    name: 'Home',
    data() {
      return {
        isLoading: false,
        langIsEng: null,
        currentPage: 1,
        total: null,
        data: {},
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        users: 0,
        videos: 0,
        teeth: require(`./../type4-0.png`),
        patientData: {},
        
        leftSideTeethAdultCounter: [
          '8', '7', '6', '5', '4', '3', '2', '1'
        ],
        rightSideTeethAdultCounter: [
          '1', '2', '3', '4', '5', '6', '7', '8'
        ],
        leftSideTeethChildCounter: [
          '#', '#', '#', 'E', 'D', 'C', 'B', 'A'
        ],
        rightSideTeethChildCounter: [
          'A', 'B', 'C', 'D', 'E', '#', '#', '#'
        ],
        teeth_up_left: [],
        teeth_up_right: [],
        teeth_lower_left: [],
        teeth_lower_right: [],
        operationsEn: [],
        operation: {},
        operationsDic: {},
        visitOperationDic: {},


        // modal
        currentTeeth: {},
        teethOperationModal: false,
        statusColor: {
          'Good': 'white',
          'good': 'white',
          'Calculs': '#F2E4D2',
          'Cares': '#FFFF00',
          'Missing': '#948A54',
          'Implant': '#FF0000',
          'Fixed prosthetics': '#974706',
          'Removable prosthetics': '#4F6228',
          
          'Post & Core': '#C4AEAD',
          'Filling': '#C4AEAD',

          'Normal': '#FFA07A',
          'Retreatment': '#FFA07A',

          'Simple': '#667C26',
          'Impaction': '#667C26',

          'PFM': '#E2A76F',
          'Zirconia': '#E2A76F',

          'Scaling': '#00B0F0'
        },
        teethOperationDic: {}
      }
    },
    methods: {
      endVisit() {
        let i = 0;
        this.visitOperationDic[this.$route.params.id].operations.forEach(element => {
          this.isLoading = true;
          this.pageIsLoading = true;
          let data = {
            cost: element.discoundCost
          }
          var successCallback = (response) => {
            this.isLoading = false;
            this.pageIsLoading = false;
            this.alertType = "success"
            this.alertText = "Success"
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            i = i + 1;
            
        if(i == this.visitOperationDic[this.$route.params.id].operations.length){
          this.updateVisitStatus();
        }
          };
          var errorCallback = () => {
            this.isLoading = false;
            this.pageIsLoading = false;
            this.alertType = "danger"
            this.alertText = "     "
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('put', '/doctor/update-operation-teeth/' + element.id, true, data, successCallback, errorCallback);
        });
      },
      updateVisitStatus() {
        this.pageIsLoading = true;
        this.isLoading = true;
        var successCallback = () => {
          this.alertType = "success"
          this.alertText = "  ."
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.$router.push({
            name: "home"
          })
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = this.langIsEng ? "Wrong .. try soon !" : "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/nurse/visits/' + this.$route.params.id, true, {'status': " "}, successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      submitOperation() {
        this.teethOperationModal = false
        this.isLoading = true;
        this.pageIsLoading = true;   
        let data = {
          'visit_id': this.$route.params.id,
          'patient_id': this.$route.params.patientId,
          'operation': this.currentTeeth.name,
          'cost': this.currentTeeth.cost
        }     
        var successCallback = (response) => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "success"
          this.alertText = "Success"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.getProfileData(this.$route.params.patientId);
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('post', '/doctor/set-operation-teeth/' + this.currentTeeth.id, true, data, successCallback, errorCallback);
      },
      deleteOperation(id){
        this.pageIsLoading = true;
        this.isLoading = true;
        if(id) {
          this.isLoading = true;
          var successCallback = () => {
            this.alertType = "success"
            this.alertText = this.langIsEng ? "Operation removed successfully." : "  ."
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            this.getProfileData(this.$route.params.patientId);
          };
          var errorCallback = () => {
            this.isLoading = false;
            this.alertType = "danger"
            this.alertText = this.langIsEng ? "Wrong .. try soon !" : "     "
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('delete', '/doctor/operation-teeth/' + id, true, null, successCallback, errorCallback);
        }
      },
      changeCurrentStatus(newStatus){
        this.currentTeeth.status = newStatus.name;
        this.currentTeeth.name = newStatus.name;
        this.currentTeeth.cost = newStatus.cost;
        this.currentTeeth.color = newStatus.color;
      },
      chengeTeeth(item, name, position){
        this.currentTeeth = item;
        this.currentTeeth.teethName = name;
        this.currentTeeth.position = position;
        this.currentTeeth.isLower = this.currentTeeth.position.includes('Lower'); 
        this.operation = item.status;
        this.teethOperationModal = true;
      },
      getProfileData(id) {
        this.isLoading = true;
        this.pageIsLoading = true;
        this.teeth_up_left = [];
        this.teeth_up_right = [];
        this.teeth_lower_left = [];
        this.teeth_lower_right = [];

        var successCallback = (response) => {
          this.patientData = response.data.data;
          for (let quarter = 0; quarter < 4; quarter++) {
            this.isLoading = true;
            this.pageIsLoading = true;
            for (let i = 0; i < 8; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 0 || i == 1 || i == 2) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 25;
                this.patientData.teeths[i].type = 4;
              }
              //  
              if(i == 3 || i == 4){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 3;
              }
              // 
              if(i == 5){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 2;
              }
              // 
              if(i == 6 || i == 7){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 1;
              }
              if(quarter == 0) {
                this.teeth_up_left.push(this.patientData.teeths[i])
              }
            }
            for (let i = 8; i < 16; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 15 || i == 14 || i == 13) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 25;
                this.patientData.teeths[i].type = 4;
              }
              if(i == 12 || i == 11){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 3;
              }
              if(i == 10){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 2;
              }
              if(i == 9 || i == 8){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 1;
              }
              if(quarter == 0) {
                this.teeth_up_right.push(this.patientData.teeths[i])
              }
            }
            for (let i = 16; i < 24; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 16 || i == 17 || i == 18) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 25;
                this.patientData.teeths[i].type = 4;
              }
              if(i == 19 || i == 20){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 3;
              }
              if(i == 21){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 2;
              }
              if(i == 22 || i == 23){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 1;
              }
              if(quarter == 0) {
                this.teeth_lower_left.push(this.patientData.teeths[i])
              }
            }
            for (let i = 24; i < 32; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 31 || i == 30 || i == 29) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 25;
                this.patientData.teeths[i].type = 4;
              }
              if(i == 28 || i == 27){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 3;
              }
              if(i == 26){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 2;
              }
              if(i == 25 || i == 24){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 20;
                this.patientData.teeths[i].type = 1;
              }

              if(quarter == 0) {
                this.teeth_lower_right.push(this.patientData.teeths[i])
              }
            }
          }
          this.patientData.visits.forEach(element => {
            if(element.id == this.$route.params.id){
              this.visitOperationDic[element.id] = element;
            }
          });
          this.visitOperationDic[this.$route.params.id].operations.forEach(element => {
            this.teethOperationDic[element.teeth_id] = element.operation
            element.discoundCost = element.cost;
          });
          
          this.isLoading = false;
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/doctor/show-patient/' + id, true, null, successCallback, errorCallback);
      },
      getOperations() {
        this.isLoading = true;
        this.pageIsLoading = true;
        this.operationsDic = {};
        var successCallback = (response) => {
          response.data.data.forEach(element => {
            element.text = element.name;
            element.value = element.name;
            this.operationsEn.push(element);
            this.operationsDic[element.id] = element.name;
          });
          this.getProfileData(this.$route.params.patientId);
          this.isLoading = false;
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/doctor/show-operations', true, null, successCallback, errorCallback);
      },



      goToVisit(item){
        this.$router.push({
          name: "visit",
          params: {
            id: item.id,
            visit: item
          }
        })
      }
    },
    created() {
      this.langIsEng = store.getters.getlang == "en";
      if(store.getters.getRole == "doctor")
        this.isDoctor = true;
      else if (store.getters.getRole == "nurse")
        this.isDoctor = false
        this.getOperations();
        
    }
  }
</script>

<style scoped>
  .teeth-img{
    cursor: pointer !important;
  }
  .isLower{
    transform: rotate(180deg);
  }
  .whiteTeeth{
    background-color: white;
  }
  .lightYellowTeeth{
    background-color: rgb(250, 250, 128);
  }
  .yellowTeeth{
    background-color: rgb(206, 206, 0);
  }
  .greenTeeth{
    background-color: rgb(64, 207, 64);
  }
  .darkGreenTeeth{
    background-color: rgb(12, 87, 37);
  }
  .redTeeth{
    background-color: #fb6767;
  }
  .blackTeeth{
    background-color: #8f8f8f;
  }
</style>
