const getters  = {
  authToken(state) {
    return state.token
  },
  username(state) {
    return state.user_name
  },
  accoutType(state) {
    return state.account_type
  },
  getlang(state){
    return state.lang
  },
  getRole(state){
    return state.role
  }
}
export default getters 
