<template>
  <div class="page-layout users">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    
    <div class="text-center text-danger my-2" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>

    <b-form @submit.stop.prevent="onSubmit" v-show="!pageIsLoading" >
      <div class="section-header mt-2">
        <h3>
          <b>{{langIsEng ? "Add new patient" : "  "}}</b>
          
        </h3>
      </div>
      <div class="form-section">
          <div class="section-header">
            <h4>
              <b>{{langIsEng ? "Personal information" : " "}}</b>
            </h4>
          </div>
          <b-row>
            <b-col lg="3" md="12">
              <b-form-group :label="langIsEng ? 'Phone' : ' '" >
                <b-form-input
                  id="visit-add-phone"
                  name="visit-add-phone"
                  v-model="$v.form.phone.$model"
                  :state="validateState('phone')"
                  aria-describedby="input-add-phone"
                ></b-form-input>
                <b-form-invalid-feedback id="input-edit-phone" v-if='!$v.form.phone.required'>
                  {{langIsEng ? "Phone is required." : "  "}}
                </b-form-invalid-feedback>
                <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.phone.minLength'>
                  {{langIsEng ? "Phone is 11 number only." : "    11 "}}
                </b-form-invalid-feedback>
                <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.phone.maxLength'>
                  {{langIsEng ? "Phone is 11 number only." : "    11 "}}
                </b-form-invalid-feedback>
                <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.phone.numeric'>
                  {{langIsEng ? "Phone is numbers." : "     "}}
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
            <b-col lg="3" md="12">
              <b-form-group id="group-visit-add-name" :label="langIsEng ? 'Patient name' : ' '" label-for="visit-add-name">
                <b-form-input
                  id="visit-add-name"
                  name="visit-add-name"
                  v-model="$v.form.name.$model"
                  :state="validateState('name')"
                  aria-describedby="input-add-name"
                ></b-form-input>
                <b-form-invalid-feedback id="input-add-name">
                  {{langIsEng ? "Name is required." : " "}}
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
            <b-col lg="3" md="12">
              <b-form-group id="group-visit-add-age" :label="langIsEng ? 'Age' : ''" label-for="visit-add-age">
                <b-form-input
                  id="visit-add-age"
                  name="visit-add-age"
                  v-model="$v.form.age.$model"
                  :state="validateState('age')"
                  aria-describedby="input-add-age"
                ></b-form-input>
                <b-form-invalid-feedback id="input-edit-age" v-if='!$v.form.age.required'>
                  {{langIsEng ? "Age is required." : " "}}
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
  
            <b-col lg="3" md="12">
              <b-form-group id="group-visit-add-national" :label="langIsEng ? 'National ID' : ' '" label-for="visit-add-national">
                <b-form-input
                  id="visit-add-national"
                  name="visit-add-national"
                  type="number"
                  v-model="form.national"
                  
                  aria-describedby="input-add-national"
                ></b-form-input>
                <!-- <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.national.required'>
                  {{langIsEng ? "National ID is required." : "  "}}
                </b-form-invalid-feedback>
                <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.national.minLength'>
                  {{langIsEng ? "National ID is 14 number only." : "    14 "}}
                </b-form-invalid-feedback>
                <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.national.maxLength'>
                  {{langIsEng ? "National ID is 14 number only." : "    14 "}}
                </b-form-invalid-feedback>
                <b-form-invalid-feedback id="input-edit-national" v-if='!$v.form.national.numeric'>
                  {{langIsEng ? "National ID is numbers." : "     "}}
                </b-form-invalid-feedback> -->
                
              </b-form-group>
            </b-col>
            <b-col lg="3" md="12">
              <b-form-group id="group-visit-add-address" :label="langIsEng ? 'Address' : ''" label-for="visit-add-address">
                <b-form-input
                  id="visit-add-address"
                  name="visit-add-address"
                  v-model="form.address"
                  
                  aria-describedby="input-add-address"
                ></b-form-input>
                <!-- <b-form-invalid-feedback id="input-edit-address" v-if='!$v.form.address.required'>
                  {{langIsEng ? "Address is required." : " "}}
                </b-form-invalid-feedback> -->
              </b-form-group>
            </b-col>
          </b-row>
      </div>
        <div class="form-section" >
          <div class="section-header">
            <h4>
              <b>{{langIsEng ? "Medical information" : " "}}</b>
            </h4>
          </div>


          <!-- row 1 -->
          <b-row>
            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.heartDisease">
                  {{langIsEng ? 'Heart disease' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.leverInflammation">
                  {{langIsEng ? 'Lever inflammation A/B/C' : '   A/B/C'}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.bloodPressure">
                  {{langIsEng ? 'BloodPressure' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.anemia">
                  {{langIsEng ? 'Anemia' : ''}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>




            <!-- row 2 -->
            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.hemophilia">
                  {{langIsEng ? 'Hemophilia' : ''}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.kidneyDisease">
                  {{langIsEng ? 'Kidney disease' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.aids">
                  {{langIsEng ? 'AIDS' : ''}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.rheumaticfever">
                  {{langIsEng ? 'Rheumatic fever' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <!-- row 3 -->
            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.pregnancy">
                  {{langIsEng ? 'Pregnancy' : ''}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.tb">
                  {{langIsEng ? 'tb' : ''}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.radiationTherapy">
                  {{langIsEng ? 'Radiation therapy' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.respiratoryDistress">
                  {{langIsEng ? 'Respiratory distress' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>



            <!-- row 4 -->
            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.chemotherapy">
                  {{langIsEng ? 'Chemotherapy' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.diabetes">
                  {{langIsEng ? 'Diabetes' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.arthritis">
                  {{langIsEng ? 'Arthritis' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.liquorDrugs">
                  {{langIsEng ? 'Liquor drugs' : ' '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <!-- row 5 -->
            <b-col lg="3" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.thyroidDiseases">
                  {{langIsEng ? 'Thyroid diseases' : '  '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>
          </b-row>
          <div class="mt-5"></div>

          <!-- question row 1 -->
          <b-row>
            <b-col lg="12" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.underMedicalCare">
                  {{langIsEng ? 'Are you currently undergoing medical care?' : '    '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>


            <b-col lg="12" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.useAsprin">
                  {{langIsEng ? 'Do you use aspirin regularly?' : '    '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="12" md="12">
              <b-form-group label-for="useAnyMedications">
                <b-form-checkbox v-model="form.useAnyMedications" id="useAnyMedications">
                  {{langIsEng ? 'Are you using any medications for any reason at the moment?' : '        '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="12" md="12">
              <b-form-group label-for="useAnyMedicationsDetails" :label="langIsEng ? 'Write down the medications you take ...' : '     ...'">
                <b-form-textarea :disabled="!form.useAnyMedications" id="useAnyMedicationsDetails" v-model="form.useAnyMedicationsDetails" rows="2" max-rows="2" ></b-form-textarea>
              </b-form-group>
            </b-col>
          </b-row>






          <div class="mt-5"></div>
          <!-- question row 2 -->
          <b-row>
            <b-col lg="12" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.bleeding">
                  {{langIsEng ? 'Are you facing the problem of continuous bleeding after surgery or extraction?' : '         '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>


            <b-col lg="12" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.dentalAnesthetic">
                  {{langIsEng ? 'Have you experienced an abnormal reaction to dental anesthetic?' : '        '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="12" md="12">
              <b-form-group label-for="drugSensitivity">
                <b-form-checkbox v-model="form.drugSensitivity" id="drugSensitivity">
                  {{langIsEng ? 'Do you mean from any sensitivity to any drug or drug?' : '         '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>

            <b-col lg="12" md="12">
              <b-form-group label-for="drugSensitivityDetails" :label="langIsEng ? 'Write down the medications you take ...' : '     ...'">
                <b-form-textarea :disabled="!form.drugSensitivity" id="drugSensitivityDetails" v-model="form.drugSensitivityDetails" rows="2" max-rows="2" ></b-form-textarea>
              </b-form-group>
            </b-col>
          </b-row>










          
          <div class="mt-5"></div>
          <!-- question row 3 -->
          <b-row>
            <b-col lg="12" md="12" class="mb-2">
              <h5>{{langIsEng ? 'Do you have these habits?' : '   '}}</h5>
            </b-col>
            <b-col lg="12" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.smoking">
                  {{langIsEng ? 'Smoking' : ''}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>


            <b-col lg="12" md="12">
              <b-form-group >
                <b-form-checkbox v-model="form.clenchingTeeth">
                  {{langIsEng ? 'Clenching hard on the teeth' : '   '}}
                </b-form-checkbox>
              </b-form-group>
            </b-col>


            <b-col lg="12" md="12">
              <b-form-group label-for="otherInfo" :label="langIsEng ? 'The latest developments in the medical situation' : '     ...'">
                <b-form-textarea id="otherInfo" v-model="form.otherInfo" rows="2" max-rows="2" ></b-form-textarea>
              </b-form-group>
            </b-col>
          </b-row>
        </div>
        <div>
          <b-button type="submit" variant="success">{{langIsEng ? 'Add' : ''}}</b-button>
          <b-button :class="langIsEng ? 'ml-2' : 'mr-2'" @click="resetForm()">{{langIsEng ? 'Reset' : ''}}</b-button>
        </div>
        
      <!-- </b-container> -->

    </b-form>
  </div>
</template>

<script>
  import { sendRequest } from "../../../utils/RequestHelper.js"
  import { validationMixin } from "vuelidate";
  import { required, minLength, maxLength, numeric, email} from "vuelidate/lib/validators";
  import store from "../../../store/index.js"

  export default {
    mixins: [validationMixin],
    data() {
      return {
        pageIsLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        lang: null,
        langIsEng: null,
        form: {
          name: null,
          phone: null,
          national: null,
          age: null,
          address: "",

          // row 1W
          heartDisease: false,
          leverInflammation: false,
          bloodPressure: false,
          anemia: false,

          // row 2
          hemophilia: false,
          kidneyDisease: false,
          aids: false,
          rheumaticfever: false,

          // row 3
          pregnancy: false,
          tb: false,
          radiationTherapy: false,
          respiratoryDistress: false,

          // row 4
          chemotherapy: false,
          diabetes: false,
          arthritis: false,
          liquorDrugs: false,

          // row 5
          thyroidDiseases: false,

          //question row 1
          underMedicalCare: false,
          useAsprin: false,
          useAnyMedications: false,
          useAnyMedicationsDetails: "",

          //question row 2
          bleeding: false,
          dentalAnesthetic: false,
          drugSensitivity: false,
          drugSensitivityDetails: "",


          smoking: false,
          clenchingTeeth: false,
          otherInfo: ""
        },
        HistoryDic: {
          heartDisease: "heartDisease",
          leverInflammation: "leverInflammation",
          bloodPressure: "bloodPressure",
          anemia: "anemia",

          // row 2
          hemophilia: "hemophilia",
          kidneyDisease: "kidneyDisease",
          aids: "aids",
          rheumaticfever: "rheumaticfever",

          // row 3
          pregnancy: "pregnancy",
          tb: "tb",
          radiationTherapy: "radiationTherapy",
          respiratoryDistress: "respiratoryDistress",

          // row 4
          chemotherapy: "chemotherapy",
          diabetes: "diabetes",
          arthritis: "arthritis",
          liquorDrugs: "liquorDrugs",

          // row 5
          thyroidDiseases: "Thyroid diseases",

          //question row 1
          underMedicalCare: "Under medical care",
          useAsprin: "Use asprin",
          useAnyMedications: "Use any medications",
          useAnyMedicationsDetails: "Use any medications details",

          bleeding: "Bleeding",
          dentalAnesthetic: "Dental anesthetic",
          drugSensitivity: "drug sensitivity",
          drugSensitivityDetails: "Drug sensitivity details",

          smoking: "Smoking",
          clenchingTeeth: "Clenching Teeth",

          otherInfo: "Other Information"
        }
      };
    },
    validations: {
      form: {
        name: {
          required
        },
        phone: {
          required,
          numeric,
          minLength: minLength(11),
          maxLength: maxLength(11)
        },
        age: {
          required
        },
      }
    },
    watch: {
    },
    methods: {
      validateExistPatientState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      resetForm() {
        this.form = {
          name: null,
          phone: null,
          national: null,
        };
        this.$nextTick(() => {
          this.$v.$reset();
        });
      },
      onSubmit() {
        this.$v.form.$touch();
        if (this.$v.form.$anyError) {
          return;
        }
        this.AddPatient();
      },
      AddPatient() {
        this.pageIsLoading = true;
        if(this.form.address == null || this.form.address == "")
          this.form.address = "-"
        let data = {
          name: this.form.name,
          age: this.form.age,
          phone: this.form.phone,
          national_id: this.form.national,
          address:this.form.address,
          medical_history: ""
        }
        delete this.form.name
        delete this.form.age
        delete this.form.phone
        delete this.form.national
        delete this.form.address
        let medical_history = ""


        const keys = Object.keys(this.form);
        const values = Object.values(this.form);

        for (let i = 0; i < values.length; i++) {
          if(keys[i] == "useAnyMedicationsDetails"){
            keys[i] = this.form.useAnyMedicationsDetails
          }
          if(keys[i] == "drugSensitivityDetails"){
            keys[i] = this.form.drugSensitivityDetails
          }
          if(keys[i] == "otherInfo"){
            keys[i] = this.form.otherInfo
          }

          if(values[i]){
            medical_history += keys[i]
            medical_history += ","
          }
          else{
            medical_history += "-"
            medical_history += ","
          }
          
        }
        medical_history = medical_history.slice(0, -1);
        data.medical_history = medical_history
       

        var successCallback = () => {
          this.pageIsLoading = false;
          this.alertType = "success"
          this.alertText = this.langIsEng ? "Patient added successfully." : "   "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.resetForm();
        };
        var errorCallback = (error) => {
          var errorMessage = "     "
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = errorMessage
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('post', '/nurse/patients', false, data, successCallback, errorCallback);
      },
    },
    created() {
      this.lang = store.getters.getlang;
      this.langIsEng = store.getters.getlang == "en";
      if(this.$route.query.phone){
        this.form.phone = this.$route.query.phone
      }
    }
  };
</script> 
