<template>
  <div class="page-layout users">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>{{langIsEng ? "Patients" : ""}}</b></h3>
    </div>
    <div class="text-center text-danger my-2" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <div class="row"  v-show="!pageIsLoading">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <div class="table-responsive">
              <table class="table table-bordered table-hover">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th>
                      <div class="tbl-data">{{langIsEng ? "Name" : ""}}</div>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Phone" : ""}}</span>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "National ID" : " "}}</span>
                    </th>
                    <th style="z-index:1">
                      <span class="tbl-data">{{langIsEng ? "Age" : ""}}</span>
                    </th>
                    <th style="z-index:1" width="10%" class="td-option">
                      <span clas="tbl-data">	
                        <b-button @click="goToRoute('addPatient')" size="sm" class="btn btn-success add-admin-btn options-table-btn" data-toggle="modal" data-target="#addAdmin" ><i class="fa fa-plus " :class="{'ml-2': !langIsEng, 'mr-2': langIsEng}"></i>
                          {{langIsEng ? "Add" : ""}}
                        </b-button>	
                      </span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="no-data-row" v-if="users.length == 0 && isLoading">
                    <td colspan="7">
                      <div class="text-center >text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </td>
                  </tr>
                  <tr class="no-data-row" v-if="users.length == 0 && !isLoading">
                    <td colspan="7">
                        
                    </td>
                  </tr>
                  <tr v-for="(item, index) in users" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <div style="carousel:pointer"  class="tbl-data" @click="goToProfile('nursePatientProfile', item.id)">
                        <b-link>
                          {{item.name}}
                        </b-link>
                      </div>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.phone}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.national_id}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.age}}</span>
                    </td>
                    <td class="td-option">
                      <div class="form-group row-option">
                        <b-button type="button" size="sm" class="btn btn-primary options-table-btn edit-options-table-btn" @click="goToEditRoute(item.id)" ><i class="fa fa-edit"></i></b-button>
                        <b-button type="button" size="sm" class="btn btn-danger options-table-btn del-options-table-btn" data-toggle="modal" data-target="#delAdmin" @click="openDeleteItem(item.id);" ><i class="fa fa-trash"></i></b-button>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <b-pagination v-model="currentPage" :total-rows="total" :per-page="10" align="center" size="sm"></b-pagination>
          </div>
        </div>
      </div>
    </div>
    <b-modal v-model="delModal" centered title=" ">
      <p class="my-4">    </p>
      <template v-slot:modal-footer>
        <div class="w-100">
          <b-button variant="primary" size="sm" class="float-left mr-1" @click="deleteItem()">
            
          </b-button>
          <b-button variant="secondary" size="sm" class="float-left" @click="delModal=false">
            
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>
<script>
  import { sendRequest }  from "../../../utils/RequestHelper.js"
  import store from "../../../store/index.js"

  export default {
    name: 'Home',
    data() {
      return {
        users:[],
        isLoading: true,
        pageIsLoading: false,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        showDismissibleAlert: false,
        currentID: null,
        delModal: false,
        currentPage: 1,
        total: null,
        from: 1,
        lang: null,
        langIsEng: null
      }
    },
    watch: {
    currentPage: {
      handler: function(value) {
        this.loadData(value);
      }
    }
  },
    methods: {
      goToProfile(name, id) {
        if(id) {
          this.$router.push({
            name,
            params: {
              id,
            }
          })
        }
      },
      goToRoute(routeName) {	
        this.$router.push({	
          name: routeName	
        })
      },  
      loadData(page){
        this.pageIsLoading = true;
        this.isLoading = true;
        var successCallback = (response) => {
          this.users = response.data.data;
          this.total = response.data.total;
          this.from = response.data.from;
          this.isLoading = false;
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/patients', true, null, successCallback, errorCallback);
      },
      openDeleteItem(id){
        if(id) {
          this.delModal = true;
          this.currentID = id;
        }
      },
      deleteItem() {
        this.pageIsLoading = true;
        this.isLoading = true;
        if(this.currentID) {
          this.isLoading = true;
          // this.delModal = false;
          // this.alertType = "success"
          // this.alertText = "    "
          // this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          // this.delModal = false;
          // this.loadData();


          var successCallback = () => {
            this.alertType = "success"
            this.alertText = "    "
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            this.delModal = false;
            this.loadData();
          };
          var errorCallback = () => {
            this.isLoading = false;
            this.alertType = "danger"
            this.alertText = "     "
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('delete', '/nurse/patients/' + this.currentID, true, null, successCallback, errorCallback);
        }
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      goToEditRoute(id){
        if(id) {
          this.$router.push({
            name: 'editPatient',
            params: {
              id
            }
          })
        }
      }
    },
    created() {
      this.loadData(this.currentPage);
      this.lang = store.getters.getlang;
      this.langIsEng = store.getters.getlang == "en";
    },
  }
</script>
<style scoped>
  @import '../../../assets/css/tableContent.css'
</style>