<template>
  <div v-if="!['login'].includes($route.name)">
    <div :lang="lang" class="header">
      <a href="#" class="nav-trigger" @click="navToogle">
        <span></span>
      </a>
      <div class="logoutBtn-div">
        <b-dropdown size="sm" id="dropdown-1" :right="langIsEng" :text="username" class="mt-2 logoutBtn"  variant="outline-secondary">
          <b-form-radio-group
            @change="changeLang()"
            style="padding-inline-start: 22px; padding-bottom: 10px"
            size="sm"
            id="btn-radios-1"
            v-model="currentLang"
            button-variant="outline-primary"
            :options="[{ text: 'En', value: 'en' }, { text: ' ', value: 'ar' },]"
            buttons
          ></b-form-radio-group>
          <b-dropdown-item @click="goToSettingsRouter('editProfile')">
            {{langIsEng ? "Edit Account" : " "}}
          </b-dropdown-item>
          <b-dropdown-item v-b-modal.modal-logout>
            {{langIsEng ? "Logout" : " "}}
          </b-dropdown-item>
        </b-dropdown>
      </div>
    </div>
    <div :lang="lang" class="side-nav" :class="{ visible: isVisible }" v-if="!['NotFound'].includes($route.name)">
      <div class="logo">
        <span>{{langIsEng ? "Admin Panel" : " "}}</span>
      </div>
      <nav style="height: -webkit-fill-available; -webkit-box-shadow:rgb(134 134 134) -6px -1px 4px -6px;-moz-box-shadow:rgb(134 134 134) -6px -1px 4px -6px; box-shadow: rgb(134 134 134) -6px -1px 4px -6px;">
        <ul>
          <li @click="isVisible = false">
            <router-link class="nav-link" to="/">
              <i class="fa fa-home" style="font-size: 19px"></i>
              <span class="hideInSmall">
                {{langIsEng ? "Home Page" : " "}}
              </span>
            </router-link>
          </li>
          <li v-if="role=='nurse'" @click="isVisible = false">
            <router-link class="nav-link" to="/add-visit" >
              <i class="fa fa-user-md"></i>
              <span class="hideInSmall">
                {{langIsEng ? "New Visit" : " "}}
              </span>
            </router-link>
          </li>
          <li v-if="role=='nurse'" @click="isVisible = false">
            <router-link class="nav-link" to="/add-patient" >
            <i class="fa fa-plus"></i>
            <span class="hideInSmall">
                {{langIsEng ? "New Patient" : " "}}
              </span>
            </router-link>
          </li>
          <li v-if="role=='nurse'" @click="isVisible = false">
            <router-link class="nav-link" to="/patients" >
              <i class="fa fa-users"></i>
              <span class="hideInSmall">
                {{langIsEng ? "Patients" : ""}}
              </span>
            </router-link>
          </li>
        </ul>
      </nav>
    </div> 
    <b-modal id="modal-logout" :title="langIsEng ? 'Logout' : ' '">
      <p class="my-4">{{langIsEng ? 'Do you want to logout?' : '   '}}</p>
      <template v-slot:modal-footer>
      <b-button size="sm" variant="secondary" @click="$bvModal.hide('modal-logout')">
        {{langIsEng ? 'Cancel' : ''}}
      </b-button>
      <b-button size="sm" variant="primary" @click="logout()">
        {{langIsEng ? 'Logout' : ''}}
      </b-button>
    </template>
    </b-modal>
  </div>
</template>

<script>
import store from "../../store/index.js"
import { sendRequest }  from "../../utils/RequestHelper.js"

export default {
  data() {
    return {
      isVisible: false,
      username: null,
      lang: null,
      langIsEng: null,
      currentLang: null,
      role: null
    };
  },
  components: {},
  watch: {
    $route(to, from) {
      this.username = this.$store.getters.username;
      this.role = this.$store.getters.getRole;
    },
  },
  methods: {
    goToSettingsRouter() {
      this.$router.push({
        name: 'editProfile'
      })
    },
    navToogle() {
      this.isVisible = !this.isVisible;
    },
    logout() {
      // var successCallback = () => {
        store.commit("LOGOUT");
        this.$router.push({
          name: "login"
        })
      // };
      // var errorCallback = () => {
      // };
      // sendRequest('get', '/admins/logout', true, null, successCallback, errorCallback);
    },
    changeLang() {
      if(this.currentLang == 'ar'){
        store.commit("SET_LANG", "ar");
      }
      else {
        store.commit("SET_LANG", "en");
      }
      location.reload();
    }
  },
  created() {
    this.role = store.getters.getRole;
    this.username = store.getters.username ? store.getters.username : ''
    this.lang = store.getters.getlang;
    this.currentLang = this.lang;
    this.langIsEng = store.getters.getlang == "en"
  }
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  background-color: #d5dae5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header {
  position: fixed;
  width: 100%;
  z-index: 3;
  height: 50px;
  background-color: #fff;
  border-bottom: 3px solid #ffffff;
  -webkit-box-shadow: 0 5px 5px -6px rgb(87, 87, 87);
  -moz-box-shadow: 0 5px 5px -6px rgb(87, 87, 87);
  box-shadow: 0 5px 5px -6px rgb(87, 87, 87);
}
.nav-link{
  padding: 0px;
  transition: 0.3s;
}
.nav-link:hover{
  padding-right: 20px;
  border-right: #35475e 4px solid;
}
/* logo in header(mobile version) and side-nav (tablet & desktop) */
.logo {
  height: 50px;
  padding: 10px;
  font-weight: 700;
}
.header .logo {
  color: #233245;
}
.side-nav .logo {
  background-color: #ffffff;
  color: #233245;
}
.header .logo {
  float: right;
}
.header .logo {
  height: 55px;
  z-index: 1;
  padding: 10px;
  font-weight: 700;
  color: #233245;
  margin-right: 40px;
}
.logo i {
  font-size: 22px;
}
 .logo span {
  margin-top: 2px;
  margin-right: 5px;
  text-transform: uppercase;
}
.logoImg{
  width: 20px;
}
.header:lang(ar) .nav-trigger {
  position: relative;
  float: right;
  width: 20px;
  height: 50px;
  right: 30px;
  display: block;
}
.header:lang(en) .nav-trigger {
  position: relative;
  float: left;
  width: 20px;
  height: 50px;
  left: 30px;
  display: block;
}
.nav-trigger span,
.nav-trigger span:before,
span:after {
  width: 20px;
  height: 2px;
  background-color: #35475e;
  position: absolute;
}
.nav-trigger span {
  top: 50%;
}
.nav-trigger span:before,
.nav-trigger span:after {
  content: "";
}
.nav-trigger span:before {
  top: -6px;
}
.nav-trigger span:after {
  top: 6px;
}
/* side navigation styles */
.side-nav {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  z-index: 1;
  display: none;
}
.side-nav.visible {
  display: block;
  position: fixed;
}
.side-nav ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.side-nav ul li {
  position: relative;
}
.router-link-exact-active{
  background-color: #ebebeb;
  border-right: #35475e 4px solid;
  padding-right: 12px !important;
}
.side-nav ul li a{
  padding: 10px 16px;
  color: #35475e;
}
.side-nav ul li.active:before {
  content: "";
  position: absolute;
  width: 4px;
  height: 100%;
  top: 0;
  right: 0;
  background-color: #fff !important;
}
.router-link-exact-active:has(li){
  content: "";
  position: absolute;
  width: 4px;
  height: 100%;
  top: 0;
  right: 0;
  background-color: #fff !important;
}
.side-nav ul li a {
  color: #35475e;
  display: block;
  text-decoration: none;
}
.side-nav ul li i {
  color: #35475e;
  min-width: 20px;
  text-align: center;
}
.side-nav:lang(ar) ul li span:nth-child(2) {
  margin-right: 10px;
  font-size: 14px;
  font-weight: 600;
}
.side-nav:lang(en) ul li span:nth-child(2) {
  margin-left: 10px;
  font-size: 14px;
  font-weight: 600;
}

/* main content styles */
.main-content {
  padding: 40px;
  margin-top: 0;
  padding: 0;
  padding-top: 50px;
  height: 100%;
}
.main-content .title {
  background-color: #eef1f7;
  border-bottom: 1px solid #b8bec9;
  padding: 10px 20px;
  font-weight: 700;
  color: #333;
  font-size: 18px;
}
.header:lang(ar) .logoutBtn {
  float: left;
  color:red;
  margin-left: 32px;
  margin-top: 10px !important;
}
.header:lang(en) .logoutBtn{
  float: right;
  color:red;
  margin-right: 32px;
  margin-top: 10px !important;
}
.logoutBtn button{
  background-color: transparent;
}

.btn-group>.btn{
  background-color: transparent !important;
}
/* set element styles to fit tablet and higher(desktop) */
@media screen and (min-width: 600px) {
  .header {
    background-color: #ffffff;
    z-index: 2;
    position: fixed;
  }
  .header .logo {
    display: none;
  }
  .nav-trigger {
    display: none;
  }
  .nav-trigger span,
  .nav-trigger span:before,
  span:after {
    background-color: #fff;
  }
  .side-nav {
    display: block;
    width: 50px;
    z-index: 2;
  }
  .side-nav ul li span:nth-child(2) {
    display: none;
  }
  .side-nav .logo i {
    padding-right: 12px;
  }
  .side-nav .logo span {
    display: none;
  }
  .side-nav ul li i {
    font-size: 15px;
  }
  .side-nav ul li a {
    text-align: center;
  }
  .main-content {
    margin-right: 70px;
  }   
  .btn-group>.btn{
    background-color: transparent !important;
  }
}
/* set element styles for desktop */
@media screen and (min-width: 1100px) {
  .side-nav {
    width: 15%;
  }
  .side-nav ul li span:nth-child(2) {
    display: inline-block;
  }
  .side-nav ul li i {
    font-size: 16px;
  }
  .side-nav ul li:lang(ar) a {
    text-align: right;
  }
  .side-nav ul li:lang(en) a {
    text-align: left;
  }
  .side-nav .logo i {
    padding-right: 0;
  }
  .side-nav .logo span {
    display: inline-block;
  }
  .main-content {
    margin-right: 200px;
  }
  .dropdown-toggle{
    color: white !important;
  }
  .btn-group>.btn{
    background-color: transparent !important;
  }
}
/* main box container */
.main {
  display: flex;
  flex-flow: row wrap;
}
.widget {
  flex-basis: 300px;
  flex-grow: 10;
  height: 300px;
  margin: 15px;
  border-radius: 6px;
  background-color: #ffffff;
  position: relative;
}
.widget .title {
  background-color: #eef1f7;
  border-bottom: 1px solid #dfe4ec;
  padding: 10px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  color: #617085;
  font-weight: 600;
}
.ad {
  width: 350px;
  height: 300px;
  border: 1px solid #ddd;
}
.btn-group>.btn{
  background-color: transparent !important;
}

@media screen and (max-width: 600px) {
  .logoutBtn{
    margin-left: 18px !important;
  }
}
</style>