<template>
  <div >
    <!-- doctor view -->
    <div v-if="isDoctor" >
      <doctor-visits></doctor-visits>
      <!-- <patient-profile></patient-profile> -->
    </div>


    <!-- nurse view -->
    <div v-else>
      <visits></visits>
    </div>
 </div>
</template>

<script>
  import store from "../store/index.js"
  import visits from "./admin/visits/Visits.vue"
  import doctorVisits from "./doctor/Visits.vue"

  export default {
    name: 'Home',
    data() {
      return {
        isDoctor: null
      }
    },
    components: {
      visits,
      doctorVisits
    },
    methods: {
    },
    created() {
      if(store.getters.getRole == "doctor")
        this.isDoctor = true;
      else if (store.getters.getRole == "nurse")
        this.isDoctor = false
    }
  }
</script>

<style scoped>
  .teeth-img{
    cursor: pointer !important;
  }
</style>
