<template>
  <div class=" pageContent">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <div class="row" >
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label>{{langIsEng ? 'Phone' : ''}}</label>
          <b-form-input :placeholder="langIsEng ? 'Phone number' : ' '" v-model="selectedPhone" @change="loadData"
          ></b-form-input>  
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label>{{langIsEng ? 'Visit status' : ' '}}</label>
          <b-form-select class="grade-selectOption" @change="loadData" v-model="selectedVisitStatus" :options="langIsEng ? visitsStatusEn : visitsStatusAr"></b-form-select>
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label for="example-datepicker">{{langIsEng ? 'Date' : ''}}</label>
          <b-form-datepicker id="example-datepicker" :placeholder="langIsEng ? 'Data' : ''" @input="loadData" v-model="selectedDate" today-button reset-button close-button class="mb-2"></b-form-datepicker>
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <br>
          <b-button type="button" style="float:right" class="btn btn-primary options-table-btn del-options-table-btn mt-3" data-toggle="modal" data-target="#delAdmin" @click="loadData(currentPage);" ><i class="fa fa-refresh"></i></b-button>
        </div>
      </div>
    </div>
    <div class="text-center text-danger" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <div class="row mt-3"  v-show="!pageIsLoading">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <div class="table-responsive">
              <table class="table table-bordered table-hover">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th>
                      <div class="tbl-data">{{langIsEng ? "Name" : ""}}</div>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Phone" : ""}}</span>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Date and time" : " "}}</span>
                    </th>
                    <th style="z-index:1">
                      <span class="tbl-data">{{langIsEng ? "Statue" : ""}}</span>
                    </th>
                    <th style="z-index:1">
                      <span class="tbl-data"></span>
                    </th>
                    <!-- <th style="z-index:1" width="10%" class="td-option">
                      <span clas="tbl-data">	
                        
                      </span>
                    </th> -->
                  </tr>
                </thead>
                <tbody>
                  <tr class="no-data-row" v-if="visits.length == 0 && isLoading">
                    <td colspan="7">
                      <div class="text-center >text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </td>
                  </tr>
                  <tr class="no-data-row" v-if="visits.length == 0 && !isLoading">
                    <td colspan="8">
                      {{langIsEng ? "No data." : "  "}}
                    </td>
                  </tr>
                  <tr v-for="(item, index) in visits" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <div class="tbl-data">{{item.patient ? item.patient.name : ''}}</div>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.patient ? item.patient.phone : ''}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.date + " - " + item.time}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">
                        <b-badge :variant="item.status==''?'warning':item.status==' '?'primary':item.status==' '?'danger':'info'">{{langIsEng ? statusDicEn[item.status] : statusDicAr[item.status]}}</b-badge>
                      </span>
                    </td>
                    <td class="td-option">
                      <div class="form-group row-option">
                        <b-button v-if="item.status==' '" @click="goToVisit(item)" type="button" size="lg" class="btn btn-success options-table-btn edit-options-table-btn">{{langIsEng ? "Visit" : ""}}</b-button>
                        <b-button @click="goToProfile(item)" type="button" size="lg" class="btn btn-primary options-table-btn edit-options-table-btn">{{langIsEng ? "Show profile" : " "}}</b-button>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <b-pagination v-model="currentPage" :total-rows="total" :per-page="10" align="center" size="sm"></b-pagination>
          </div>
        </div>
      </div>
    </div>

    <b-modal v-model="confirmUpdate" centered title=" ">
      <p class="my-4">{{langIsEng ? 'Are you sure?' : '      "' + currentStatus + '" '}}</p>
      <template v-slot:modal-footer>
        <div class="w-100">
          <b-button variant="primary" @click="updateVisitStatus()" size="sm" class="float-left mr-1">
            
          </b-button>
          <b-button variant="secondary" size="sm" class="float-left" @click="confirmUpdate=false">
            
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>
<script>
  import { sendRequest }  from "../../utils/RequestHelper.js"
  import store from "../../store/index.js"

  export default {
    name: 'Home',
    data() {
      return {
        visits:[],
        isLoading: true,
        pageIsLoading: false,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        showDismissibleAlert: false,
        currentID: null,
        currentStatus: null,
        delModal: false,
        currentPage: 1,
        total: null,
        from: 1,
        lang: null,
        langIsEng: null,
        statusDicAr: {
          '': "",
          ' ': " ",
          ' ': " ",
          ' ': " "

        },
        statusDicEn: {
          '': "Waiting",
          ' ': "With doctor",
          ' ': "Ready to pay",
          ' ': "Finished"

        },
        selectedDate: "",
        selectedVisitStatus: " ",
        visitsStatusAr: [
           {
            text: " ",
            value: null
          },
          {
            text: "",
            value: ""
          },
          {
            text: " ",
            value: " "
          },
          {
            text: " ",
            value: " "
          },
          {
            text: " ",
            value: " "
          }
        ],
        visitsStatusEn: [
          {
            text: "Show all",
            value: null
          },
          {
            text: "Waiting",
            value: ""
          },
          {
            text: "With doctor",
            value: " "
          },
          {
            text: "Ready to pay",
            value: " "
          },
          {
            text: "Finished",
            value: " "
          }
        ],


        selectedPhone: null,

        confirmUpdate: false


        
      }
    },
    watch: {
      currentPage: {
        handler: function(value) {
          this.loadData(value);
        }
      }
    },
    methods: {
      goToProfile(item) {
        this.$router.push({	
          name: 'patientProfile',
          params: {
            id: item.patient_id
          }
        })
      },
      goToVisit(item) {
        this.$router.push({	
          name: 'patientVisit',
          params: {
            id: item.id,
            patientId: item.patient_id,

          }
        })
      },
      goToRoute(routeName) {	
        this.$router.push({	
          name: routeName	
        })
      },
      loadData(){
        this.pageIsLoading = true;
        this.isLoading = true;

        // status filter
        let status = ""
        if(this.selectedVisitStatus != null) {
          status = "&status=" + this.selectedVisitStatus;
        }

        // doctor filter
        let doctor = ""
        if(this.selectedDoctor != null) {
          doctor = "&doctor_id=" + this.selectedDoctor;
        }

        // phone filter
        let phone = ""
        if(this.selectedPhone != null) {
          phone = "&phone=" + this.selectedPhone;
        }

        // date filter
        let date = ""
        if(this.selectedDate != null && this.selectedDate != '') {
          date = "&date=" + this.selectedDate;
        }
        this.visits = []
        var successCallback = (response) => {
          this.visits = response.data.data;
          this.total = response.data.total;
          this.from = response.data.from;
          this.isLoading = false;
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/doctor/visits?' + status + phone + doctor + date, true, null, successCallback, errorCallback);
      },
      confirmupdateVisit(id, status) {
        this.currentID = id;
        this.currentStatus = status;
        this.confirmUpdate = true;
      },
      updateVisitStatus() {
        this.confirmUpdate = false;
        this.pageIsLoading = true;
        this.isLoading = true;
        var successCallback = () => {
          this.alertType = "success"
          this.alertText = this.langIsEng ? "Patient go to doctor." : "   -  ."
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.loadData();
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = this.langIsEng ? "Wrong .. try soon !" : "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/doctor/visits/' + this.currentID, true, {'status': this.currentStatus}, successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      }
    },
    created() {
      const d = new Date();
      let year = d.getFullYear();
      let month = d.getMonth() + 1;
      let day = d.getDate();
      this.selectedDate = year + "-" + month + "-" + day;


      this.loadData(this.currentPage);
      this.lang = store.getters.getlang;
      this.langIsEng = store.getters.getlang == "en";
    },
  }
</script>
<style scoped>
  @import '../../assets/css/tableContent.css';
</style>