<template>
  <div class="home">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div dir="rtl" v-if="patientData">
      <div class="text-center >text-danger my-2" v-if="isLoading">
        <b-spinner class="align-middle"></b-spinner>
      </div>
      <div v-if="!isLoading && patientData.visits.length == 1" >
      </div>
      <div dir="ltr" v-if="!isLoading">
        <h6><b>Name: </b>{{patientData.name}} / <b>Age: </b>{{patientData.age}}  / <b>Phone: </b>{{patientData.phone}} / <b-button @click="goToProfile(patientData.id)" type="button" size="sm" class="btn btn-secondary options-table-btn edit-options-table-btn">{{langIsEng ? "Patient Medical File" : " "}}</b-button></h6>
        <h6 v-if="patientData.visits.length == 1"><b-badge variant="info">First visit.</b-badge></h6>
        <h6 v-else><b-badge variant="info">Last visit: {{patientData.visits[1].date}}</b-badge></h6>
        <h6></h6>
      </div>
      <div class="row" style="background-color: #acd5d1; text-align:center;" v-if="!isLoading">
        <div class="col-lg-6 col-md-12 col-sm-12 mt-4">
          <b-button @click="changeAllTeeth('Calculs')" type="button" size="lg" class="btn btn-primary options-table-btn edit-options-table-btn">Make all teeth Calculs</b-button>
          <b-button @click="changeAllTeeth('Good')" type="button" size="lg" class="btn btn-primary options-table-btn edit-options-table-btn">Make all teeth Good</b-button>
        </div>
        <div class="col-lg-6 col-md-12 col-sm-12 mt-4" v-if="patientData.visits.length == 1">
          <b-badge variant="warning">.You need to complete patient profile</b-badge>
          <br><br>
        </div>
      </div>
      <div class="row" style="background-color: #acd5d1; text-align:center;" v-if="!isLoading">
        
        <div class="col-lg-6 col-md-6 col-sm-6 mt-4">
          <h6>Upper Left</h6>
          <div class="row" v-if="!isLoading" style="align-items:flex-end;border-right:3px solid #9aadab; justify-content:flex-end ;border-bottom:1px solid #9aadab">

            <div v-for="(item, i) in teeth_up_left" :title="item.status" v-if="patientData.age > 12 && leftSideTeethAdultCounter[i] || leftSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
              <b-img class="teeth-img" 
              :style="'background-color:' + statusColor[item.status]"
              @click="chengeTeeth(item, patientData.age > 12 ? leftSideTeethAdultCounter[i] : leftSideTeethChildCounter[i], 'Upper Left')" :width="item.width" :src="item.imgSrc"></b-img>
              <p>{{patientData.age > 12 ? leftSideTeethAdultCounter[i] : leftSideTeethChildCounter[i]}}</p>
            </div>
            
          </div>
        </div>
        <div class="col-lg-6 col-md-6 col-sm-12 mt-4">
          <h6>Upper Right</h6>
          <div class="row" v-if="!isLoading" style="align-items:flex-end;border-right:3px solid #9aadab; border-bottom:1px solid #9aadab">

            <div v-for="(item, i) in teeth_up_right" :title="item.status" v-if="patientData.age > 12 && rightSideTeethAdultCounter[i] || rightSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
              <b-img class="teeth-img" :style="'background-color:' + statusColor[item.status]"  @click="chengeTeeth(item, patientData.age > 12 ? rightSideTeethAdultCounter[i] : rightSideTeethChildCounter[i], 'Upper Right')" :width="item.width" :src="item.imgSrc"></b-img>
              <p>{{patientData.age > 12 ? rightSideTeethAdultCounter[i] : rightSideTeethChildCounter[i]}}</p>
            </div>

          </div>
        </div>
      </div>



      <div class="row" style="background-color: #acd5d1; text-align:center;" v-if="!isLoading">
        <div class="col-lg-6 col-md-6 col-sm-6">
          <div class="row" v-if="!isLoading" style="align-items:flex-start;border-right:3px solid #9aadab; justify-content:flex-end ;border-top:2px solid #9aadab">

            <div v-for="(item, i) in teeth_lower_left" :title="item.status" v-if="patientData.age > 12 && leftSideTeethAdultCounter[i] || leftSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
              <p>{{patientData.age > 12 ? leftSideTeethAdultCounter[i] : leftSideTeethChildCounter[i]}}</p>
              <b-img class="teeth-img" :style="'background-color:' + statusColor[item.status]" style="transform: rotate(180deg);" @click="chengeTeeth(item, patientData.age > 12 ? leftSideTeethAdultCounter[i] : leftSideTeethChildCounter[i], 'Lower Left')" :width="item.width" :src="item.imgSrc"></b-img>
            </div>
            <br>
          </div>
          <br>
          <h6>Lower Left</h6>
        </div>
        <div class="col-lg-6 col-md-6 col-sm-12">
          <div class="row" v-if="!isLoading" style="align-items:flex-start;border-right:3px solid #9aadab; border-top:2px solid #9aadab">

            <div v-for="(item, i) in teeth_lower_right" :title="item.status" v-if="patientData.age > 12 && rightSideTeethAdultCounter[i] || rightSideTeethChildCounter[i] != '#'" :key="i" class="col-lg-1 col-md-1 col-sm-1 mt-4">
              <p>{{patientData.age > 12 ? rightSideTeethAdultCounter[i] : rightSideTeethChildCounter[i]}}</p>
              <b-img class="teeth-img" :style="'background-color:' + statusColor[item.status]" style="transform: rotate(180deg);" @click="chengeTeeth(item, patientData.age > 12 ? rightSideTeethAdultCounter[i] : rightSideTeethChildCounter[i], 'Lower Right')" :width="item.width" :src="item.imgSrc"></b-img>
            </div>

          </div>
          <br>
          <h6>Lower Right</h6>
        </div>
      </div>
    </div>

    <div class="row mt-3" v-show="!pageIsLoading">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <h5>Visits</h5>
            <div class="table-responsive">
              <table class="table table-bordered table-hover">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th width=20%>
                      <span class="tbl-data">{{langIsEng ? "Date and time" : " "}}</span>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Operations" : ""}}</span>
                    </th>
                    <th style="z-index:1" width="10%">
                      <span class="tbl-data"></span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in patientData.visits" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <span class="tbl-data">{{item.date + " - " + item.time}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">
                        <div>
                          <b-badge v-for="i in item.operations" :key="i.id" variant="secondary" class="mr-2">{{i.operation}}</b-badge>
                        </div>
                      </span>
                    </td>
                    <td class="td-option">
                      <div class="form-group row-option">
                        <b-button v-if="item.status==' '"  @click="goToVisit(item)" type="button" size="lg" class="btn btn-success options-table-btn edit-options-table-btn">{{langIsEng ? "Current Visit" : " "}}</b-button>
                        <b-button v-else @click="goToOldVisit(item)" type="button" size="lg" class="btn btn-primary options-table-btn edit-options-table-btn">{{langIsEng ? "Show Visit" : " "}}</b-button>

                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <b-pagination v-model="currentPage" :total-rows="total" :per-page="10" align="center" size="sm"></b-pagination>
          </div>
        </div>
      </div>
    </div>



    <b-modal v-model="teethOperationModal" :title="langIsEng ? 'Operation' : ''">
      <div class="my-4">
        <b-row>
          <b-col class="pt-4" style="background-color: #acd5d1; text-align:center;">
              <b-img class="teeth-img mb-1" style=" cursor:arrow !important" :style="'background-color:' + currentTeeth.color"  
              :width="currentTeeth.width" :src="currentTeeth.imgSrc"></b-img>
              <p>{{currentTeeth.position + " - " + currentTeeth.teethName}}</p>
          </b-col>
          <b-col lg="12" md="12">
            <b-form-group id="group-visit-add-doctor" :label="langIsEng ? 'Status' : ''">
              <b-form-select
                @change="changeCurrentStatus(operation)"
                name="visit-add-doctor"
                v-model="operation"
                :options="operationsEn"
              ></b-form-select>
            </b-form-group>
          </b-col>
        </b-row>
      </div>
      <template v-slot:modal-footer>
      <b-button size="sm" variant="secondary" @click="teethOperationModal = false">
        {{langIsEng ? 'Cancel' : ''}}
      </b-button>
      <b-button size="sm" variant="primary" @click="submitOperation()">
        {{langIsEng ? 'Save' : ''}}
      </b-button>
    </template>
    </b-modal>
 </div>
</template>

<script>
  import { sendRequest }  from "../../utils/RequestHelper.js"
  import store from "../../store/index.js"

  export default {
    name: 'Home',
    data() {
      return {
        isLoading: false,
        langIsEng: null,
        currentPage: 1,
        total: null,
        data: {},
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        users: 0,
        videos: 0,
        teeth: require(`./../type4-0.png`),
        patientData: {},
        
        leftSideTeethAdultCounter: [
          '8', '7', '6', '5', '4', '3', '2', '1'
        ],
        rightSideTeethAdultCounter: [
          '1', '2', '3', '4', '5', '6', '7', '8'
        ],
        leftSideTeethChildCounter: [
          '#', '#', '#', 'E', 'D', 'C', 'B', 'A'
        ],
        rightSideTeethChildCounter: [
          'A', 'B', 'C', 'D', 'E', '#', '#', '#'
        ],
        teeth_up_left: [],
        teeth_up_right: [],
        teeth_lower_left: [],
        teeth_lower_right: [],
        operationsEn: [],
        operation: {},
        operationsDic: {},


        // modal
        currentTeeth: {},
        teethOperationModal: false
      }
    },
    methods: {
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      submitOperation() {
        this.teethOperationModal = false
        this.isLoading = true;
        this.pageIsLoading = true;        
        var successCallback = (response) => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "success"
          this.alertText = "Success"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/doctor/initial-exam/' + this.currentTeeth.id, true, {'teeth_status': this.currentTeeth.status}, successCallback, errorCallback);
      },
      changeCurrentStatus(newStatus){
        this.currentTeeth.status = newStatus.name;
        this.currentTeeth.color = newStatus.color
      },
      chengeTeeth(item, name, position){
        this.currentTeeth = item;
        this.currentTeeth.teethName = name;
        this.currentTeeth.position = position;
        this.currentTeeth.isLower = this.currentTeeth.position.includes('Lower'); 
        this.operation = item.status;
        this.teethOperationModal = true;
      },
      getProfileData(id) {
        this.isLoading = true;
        this.pageIsLoading = true;
        this.teeth_up_left = [];
        this.teeth_up_right = [];
        this.teeth_lower_left = [];
        this.teeth_lower_right = [];

        var successCallback = (response) => {
          this.patientData = response.data.data;
          for (let quarter = 0; quarter < 4; quarter++) {
            this.isLoading = true;
            this.pageIsLoading = true;
            for (let i = 0; i < 8; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 0 || i == 1 || i == 2) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 40;
                this.patientData.teeths[i].type = 4;
              }
              //  
              if(i == 3 || i == 4){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 3;
              }
              // 
              if(i == 5){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 2;
              }
              // 
              if(i == 6 || i == 7){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 1;
              }
              if(quarter == 0) {
                this.teeth_up_left.push(this.patientData.teeths[i])
              }
            }
            for (let i = 8; i < 16; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 15 || i == 14 || i == 13) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 40;
                this.patientData.teeths[i].type = 4;
              }
              if(i == 12 || i == 11){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].type = 40;
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 3;
              }
              if(i == 10){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 2;
              }
              if(i == 9 || i == 8){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 1;
              }
              if(quarter == 0) {
                this.teeth_up_right.push(this.patientData.teeths[i])
              }
            }
            for (let i = 16; i < 24; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 16 || i == 17 || i == 18) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 40;
                this.patientData.teeths[i].type = 4;
              }
              if(i == 19 || i == 20){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 3;
              }
              if(i == 21){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 2;
              }
              if(i == 22 || i == 23){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 1;
              }
              if(quarter == 0) {
                this.teeth_lower_left.push(this.patientData.teeths[i])
              }
            }
            for (let i = 24; i < 32; i++) {
              this.isLoading = true;
              this.pageIsLoading = true;
              if(i == 31 || i == 30 || i == 29) {
                // 
                this.patientData.teeths[i].imgSrc = require(`./../type4-0.png`);
                this.patientData.teeths[i].width = 40;
                this.patientData.teeths[i].type = 4;
              }
              if(i == 28 || i == 27){
                this.patientData.teeths[i].imgSrc = require(`./../type3-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 3;
              }
              if(i == 26){
                this.patientData.teeths[i].imgSrc = require(`./../type2-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 2;
              }
              if(i == 25 || i == 24){
                this.patientData.teeths[i].imgSrc = require(`./../type1-0.png`);
                this.patientData.teeths[i].width = 30;
                this.patientData.teeths[i].type = 1;
              }

              if(quarter == 0) {
                this.teeth_lower_right.push(this.patientData.teeths[i])
              }
            }
          }
          
          this.isLoading = false;
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/doctor/show-patient/' + id, true, null, successCallback, errorCallback);
      },
      getOperations() {
        this.isLoading = true;
        this.pageIsLoading = true;
        this.operationsEn = [
          { text: "Good",
            value: {
              name: "Good",
              color: "white"
            },
            name: "Good",
            color: "white"
          },
          {
            text: "Calculs",
            value: {
              name: "Calculs",
              color: "#F2E4D2"
            },
            name: "Calculs",
            color: "#F2E4D2"
          },
          {
            text: "Cares",
            value: {
              name: "Cares",
              color: "#FFFF00"
            },
            name: "Cares",
            color: "#FFFF00"
          },
          {
            text: "Missing",
            value: {
              name: "Missing",
              color: "#948A54"
            },
            name: "Missing",
            color: "#948A54"
          },
          {
            text: "Implant",
            value: {
              name: "Implant",
              color: "#FF0000"
            },
            name: "Implant",
            color: "#FF0000"
          },
          {
            text: "Fixed prosthetics",
            value: {
              name: "Fixed prosthetics",
              color: "#974706"
            },
            name: "Fixed prosthetics",
            color: "#974706"
          },
          {
            text: "Removable prosthetics",
            value: {
              name: "Removable prosthetics",
              color: "#4F6228"
            },
            name: "Removable prosthetics",
            color: "#4F6228"
          },
        
        ];
        this.statusColor = {
          "Good": "white",
          "good": "white",
          "Calculs": "#F2E4D2",
          "Cares": "#FFFF00",
          "Missing": "#948A54",
          "Implant": "#FF0000",
          "Fixed prosthetics": "#974706",
          "Removable prosthetics": "#4F6228",


          'Post & Core': '#C4AEAD',
          'Filling': '#C4AEAD',

          'Normal': '#FFA07A',
          'Retreatment': '#FFA07A',

          'Simple': '#667C26',
          'Impaction': '#667C26',

          'PFM': '#E2A76F',
          'Zirconia': '#E2A76F',

          'Scaling': '#00B0F0'
        }
      },

      goToProfile(id) {
        this.$router.push({
          name: "patientProfileMedical",
          params: {
            id: id,
          }
        })
      },

      goToVisit(item){
        this.$router.push({
          name: "patientVisit",
          params: {
            id: item.id,
            patientId: item.patient_id
          }
        })
      },
      goToOldVisit(item) {
        this.$router.push({
          name: "patientOldVisit",
          params: {
            id: item.id,
            patientId: item.patient_id
          }
        })
      },

      changeAllTeeth(status) {


        this.isLoading = true;
        var successCallback = (response) => {
          this.isLoading = false;
          this.alertType = "success"
          this.alertText = "All teeth are '" + status + "' now."
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.getProfileData(this.$route.params.id);
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/doctor/set-teeth-status/' + this.$route.params.id, true, {'status': status}, successCallback, errorCallback);          
      }
    },
    created() {
      this.langIsEng = store.getters.getlang == "en";
      if(store.getters.getRole == "doctor")
        this.isDoctor = true;
      else if (store.getters.getRole == "nurse")
        this.isDoctor = false
        this.getOperations();
        this.getProfileData(this.$route.params.id);
    }
  }
</script>

<style scoped>
  .teeth-img{
    cursor: pointer !important;
  }
  .isLower{
    transform: rotate(180deg);
  }
  .whiteTeeth{
    background-color: white;
  }
  .lightYellowTeeth{
    background-color: rgb(250, 250, 128);
  }
  .yellowTeeth{
    background-color: rgb(206, 206, 0);
  }
  .greenTeeth{
    background-color: rgb(64, 207, 64);
  }
  .darkGreenTeeth{
    background-color: rgb(12, 87, 37);
  }
  .redTeeth{
    background-color: #fb6767;
  }
  .blackTeeth{
    background-color: #8f8f8f;
  }
</style>
