<template>
  <div class="page-layout users">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    
    <div class="text-center text-danger my-2" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit" v-show="!pageIsLoading">
      <div class="form-section">
        <div class="section-header">
          <h4>
            <b>{{langIsEng ? "Personal information" : " "}}</b>
          </h4>
        </div>
        <b-row>
          <b-col lg="4" md="12">
            <b-form-group :label="langIsEng ? 'Phone' : ' '" >
              <b-form-input
                id="visit-add-phone"
                name="visit-add-phone"
                v-model="phone"
                debounce="500"
                @keyup.enter="phoneCheck"
              ></b-form-input>
              <div style="color:red;margin-top: .25rem;font-size: 80%;color: #dc3545;" v-if="phoneError">
                {{langIsEng ? "Phone length is 11 number" : "    11 "}}
              </div>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12" v-if="phoneIsRight == 'notExisted'">
            <b-badge variant="danger">{{langIsEng ? "Patient wasn't existed, need to add new account." : "  ,    ."}}</b-badge>
            <br><br>
            <b-button @click="goToRoute('addPatient')" variant="primary">{{langIsEng ? 'Add New Patient' : '  '}}</b-button>
          </b-col>
          <b-col lg="12" md="12" v-if="phoneIsRight == true">
            <div class="mt-2"></div>
            <label>  </label>
            <b-form-group>
              <b-form-radio 
              v-for="item in accounts" 
              :key="item.id" 
              v-model="$v.existPatientForm.patient_id.$model"
              :value="item.id">
                {{item.name + ", " + item.age + ' '}}
              </b-form-radio>
              <div style="color:red;margin-top: .25rem;font-size: 80%;color: #dc3545;" v-if="existPatientForm.patient_id == null">
                {{langIsEng ? "Patient name is required." : "  "}}
              </div>
            </b-form-group>
            <b-button @click="goToRoute('addPatient')" size="sm" variant="success">{{langIsEng ? 'Add new patient with same phone' : '    '}}</b-button>
          </b-col>
        </b-row>
      </div>
      <div class="form-section" v-if="phoneIsRight == true">
        <div class="section-header" >
          <h4>
            <b>{{langIsEng ? "Visit information" : ""}}</b>
          </h4>
        </div>
        <b-row>
          <b-col lg="4" md="12" v-if="phoneIsRight == true">
            <b-form-group id="group-visit-add-doctor" :label="langIsEng ? 'Doctor' : ''" label-for="visit-add-doctor">
              <b-form-select
                id="visit-add-doctor"
                name="visit-add-doctor"
                v-model="$v.existPatientForm.doctor.$model"
                :options="doctors"
                :state="validateExistPatientState('doctor')"
                aria-describedby="input-add-doctor"
              ></b-form-select>
              <b-form-invalid-feedback id="input-add-doctor" v-if='!$v.existPatientForm.doctor.required'>
                {{langIsEng ? "Doctor is required." : " "}}
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="4" md="12">
            <label for="example-datepicker">{{langIsEng ? 'Visit date' : ' '}}</label>
            <b-form-datepicker class="date-textbox" id="visit-add-date"
              name="visit-add-date"
              v-model="$v.existPatientForm.date.$model"
              :state="validateExistPatientState('date')"
              aria-describedby="input-add-date"
              today-button reset-button close-button >
              </b-form-datepicker>
            <b-form-invalid-feedback id="input-add-date" v-if='!$v.existPatientForm.date.required'>
              {{langIsEng ? "Date is required." : "  "}}
            </b-form-invalid-feedback>
          </b-col>
          <b-col lg="4" md="12">
            <label for="example-datepicker">{{langIsEng ? 'Visit time' : ' '}}</label>
            <b-form-timepicker class="time-textbox" id="visit-add-time"
              name="visit-add-time"
              v-model="$v.existPatientForm.time.$model"
              :state="validateExistPatientState('time')"
              aria-describedby="input-add-time">
              </b-form-timepicker>
            <b-form-invalid-feedback id="input-add-time" v-if='!$v.existPatientForm.time.required'>
              {{langIsEng ? "Time is required." : "  "}}
            </b-form-invalid-feedback>
          </b-col>
        </b-row>
        <div>
          <b-button type="submit" variant="success">{{langIsEng ? 'Add New Visit' : ' '}}</b-button>
        </div>
        
      </div>
      
    </b-form>
  </div>
</template>

<script>
  import { sendRequest } from "../../../utils/RequestHelper.js"
  import { validationMixin } from "vuelidate";
  import { required, minLength, email} from "vuelidate/lib/validators";
  import store from "../../../store/index.js"

  export default {
    mixins: [validationMixin],
    data() {
      return {
        pageIsLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        doctors: [],
        doctor: [],
        accounts: [],
        phone: null,
        existPatientForm: {
          doctor: null,
          date: null,
          time: null,
          patient_id: null
        },
        phoneIsRight: false,
        selectedPatient: null,
        phoneError: false
      };
    },
    validations: {
      existPatientForm: {
        patient_id: {
          required
        },
        doctor: {
          required
        },
        date: {
          required
        },
        time: {
          required
        },
      }
    },
    methods: {
      phoneCheck() {
        if(this.phone != null && this.phone.length == 11) {
          this.phoneError = false;
          this.pageIsLoading = true;
          this.getAccounts();
        }
        else{
          this.phoneError = true;
        }
      },
      validateExistPatientState(name) {
        const { $dirty, $error } = this.$v.existPatientForm[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      resetForm() {
        this.existPatientForm = {
          patient_id: null,
          doctor: null,
          date: null,
          time: null
        };
        this.$nextTick(() => {
          this.$v.$reset();
        });
      },
      onSubmit() {
        this.$v.existPatientForm.$touch();
        if (this.$v.existPatientForm.$anyError) {
          return;
        }
        this.addVisit();
      },
      getAccounts() {
        this.pageIsLoading = true;
        var successCallback = (response) => {
          this.accounts = response.data.data;
          if(this.accounts.length > 0) {
            if(this.accounts.length == 1){
              this.existPatientForm.patient_id = this.accounts[0].id;
            }
            this.alertType = "success"
            this.alertText = this.langIsEng ? "Existed patient" : "  "
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            this.phoneIsRight = true;
            this.pageIsLoading = false;
            const d = new Date();
            let year = d.getFullYear();
            let month = d.getMonth() + 1;
            let day = d.getDate();
            this.existPatientForm.date = year + "-" + month + "-" + day;

            let hours = d.getHours();
            let minuits = d.getMinutes();
            let seconds = d.getSeconds();
            this.existPatientForm.time = hours + ":" + minuits + ":" + seconds;
          }
          else {
            this.alertType = "warning"
            this.alertText = this.langIsEng ? "Not existed" : "   "
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            this.phoneIsRight = "notExisted";
            this.pageIsLoading = false;
          }
          
        };
        var errorCallback = () => {
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/patients?phone=' + this.phone, true, null, successCallback, errorCallback);
      },
      addVisit() {
        this.pageIsLoading = true;
        this.existPatientForm.doctor_id = this.existPatientForm.doctor;
        this.existPatientForm.nurse_id = 1;
        var successCallback = () => {
          this.pageIsLoading = false;
          this.phoneIsRight = false;
          this.alertType = "success"
          this.alertText = this.langIsEng ? "Visit added successfuly." : "    "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.resetForm();
        };
        var errorCallback = (error) => {
          var errorMessage = "     "
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = errorMessage
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('post', '/nurse/visits', false, this.existPatientForm, successCallback, errorCallback);
      },
      getDoctors(){
        this.pageIsLoading = true;
        this.doctors = [];
        var successCallback = (response) => {
          let doctors = response.data.data;
          doctors.forEach(doctor => {
            doctor.text = (this.langIsEng ? "Dr/ " : "/ " ) + doctor.name;
            doctor.value = doctor.id;
            this.doctors.push(doctor)
          });
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/doctors', true, null, successCallback, errorCallback);
      },
      goToRoute(routeName) {	
        let query = ""
        if(this.phone.length == 11){
          query = {
            phone: this.phone
          }
        }
        this.$router.push({	
          name: routeName,
          query
        })
      },
    },
    created() {
      this.lang = store.getters.getlang;
      this.langIsEng = store.getters.getlang == "en";
      this.doctor = this.langIsEng ? this.accountStatusEn : this.accountStatusAr;
      this.getDoctors();
    }
  };
</script>
