<template>
  <div>
    <b-form @submit.stop.prevent="onSubmit">
      <b-container class="bv-example-row">
        <div class="text-center text-danger my-2" :class="{'alert-div-padding': isLoading}" v-show="isLoading">
          <b-spinner class="align-middle"></b-spinner>
        </div>  
        <b-row v-show="!isLoading">
          <b-col lg="12" md="12" class="alert-div">
            <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
              {{alertText}}
            </b-alert>
            <br><br>
          </b-col>
          <br><br>
          <b-col lg="12" md="12">
            <b-form-group id="group-admin-user_name" label=" " label-for="admin-user_name">
              <b-form-input
                id="admin-user_name"
                name="admin-user_name"
                v-model="$v.form.user_name.$model"
                :state="validateState('user_name')"
                aria-describedby="input-user_name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-user_name" v-if='!$v.form.user_name.required'>
                  
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <b-form-group id="group-admin-password" label=" " label-for="admin-password">
              <b-form-input
                id="admin-password"
                name="admin-password"
                v-model="$v.form.password.$model"
                :state="validateState('password')"
                aria-describedby="input-password"
                type="password"
              ></b-form-input>
              <b-form-invalid-feedback id="input-password" v-if='!$v.form.password.required'>
                  
              </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="   ">
              <b-form-radio-group v-model="form.selectedRole" >
                <b-form-radio value="doctor"></b-form-radio>
                <b-form-radio value="nurse"></b-form-radio>
              </b-form-radio-group>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <div class="form-footer">
              <b-button class="login-btn" type="submit" variant="primary">  </b-button>
              <br>
              <!-- <b-button @click="submitForm('nurse')" class="login-btn" type="submit" variant="primary">    </b-button> -->
            </div>
            <br>
          </b-col>
        </b-row>
      </b-container>
    </b-form>
  </div>
</template>

<script>
  import store from "../store/index.js"
  import { sendRequest }  from "../utils/RequestHelper.js"
  import { validationMixin } from "vuelidate";
  import { required, user_name,} from "vuelidate/lib/validators";

  export default {
    mixins: [validationMixin],
    data() {
      return {
        isLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        form: {
          user_name: null,
          password: null,
          selectedRole: "doctor"
        }
      }
    },
    validations: {
      form: {
        user_name: {
          required
        },
        password: {
          required
        },
      }
    },
    methods: {
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      submitForm() {
        this.onSubmit();
      },
      onSubmit() {
        this.$v.form.$touch();
        if (this.$v.form.$anyError) {
          return;
        }
        this.login();
      },
      login() {
        this.isLoading = true;
        var successCallback = (response) => {
          store.commit("SET_ROLE", this.form.selectedRole);
          store.commit("SET_USERNAME", this.form.selectedRole);
          store.commit("SET_LANG", (this.form.selectedRole == "doctor" ? "en" : "ar"));
          store.commit("SET_TOKEN", response.data.data.token);
          this.$router.push({
            name: "home"
          })
          if(this.form.selectedRole == 'nurse'){
            store.commit("SET_LANG", "ar");
            location.reload();
          }
          else{
            store.commit("SET_LANG", "en");
            location.reload();
          }
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "      "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('post', '/' + this.form.selectedRole + '/login', false, this.form , successCallback, errorCallback, true);
      },
    },
    created() {
      if(store.getters.authToken) {
        this.$router.push({
          name: 'home'
        })
      }
      else{
        store.commit("SET_LANG", "ar");
      }
    }
  }
</script>

<style>
  .login-page{
    width: 100% !important;
  }
  .login-panel{
    width: 35%;
    margin: auto;
  }
  .panel-heading{
    text-align: center;
  }
  .alert-div{
    margin-top: -130px;
  }
  .alert-div-padding{
    padding: 130px 0px;
  }
  .form-footer{
    padding-top: 10px;
  }
  .login-btn, .createAccount-link{
    display: block;
    text-align: center;
  }
</style>