import store from "../store/index.js"
const isLoggedIn = function (from, to, next) {
  if(!store.getters.authToken) {
    store.commit("LOGOUT");
    next({name:'login'})
  }
  else {
    next();
  }
};

const isNurse = function (from, to, next) {
  if(!store.getters.authToken) {
    store.commit("LOGOUT");
    next({name:'login'})
  }
  else {
    if(store.getters.getRole != "nurse"){
      next({name:'home'})
    }
    next();
  }
};

const isDoctor = function (from, to, next) {
  if(!store.getters.authToken) {
    store.commit("LOGOUT");
    next({name:'login'})
  }
  else {
    if(store.getters.accoutType != "doctor"){
      next({name:'home'})
    }
    next();
  }
};

export default {
  isLoggedIn,
  isNurse,
  isDoctor
};