import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'

// pages
import Home from '../views/Home.vue'
import NotFound from '../views/NotFound.vue'

// profile
import EditProfile from '../views/admin/profile/EditProfile.vue'



// nurse
import AddVisit from '../views/admin/visits/AddVisit.vue'
import UpdateVisit from '../views/admin/visits/UpdateVisit.vue'

// Patients
import Patients from '../views/admin/patients/Patients.vue'
import NursePatientProfile from '../views/admin/patients/Profile.vue'
import AddPatient from '../views/admin/patients/AddPatient.vue'
import EditPatient from '../views/admin/patients/EditPatient.vue'



// doctor
import PatientProfile from '../views/doctor/PatientProfile.vue'
import PatientProfileMedical from '../views/doctor/PatientProfileMedical.vue'
import PatientVisit from '../views/doctor/Visit.vue'
import PatientOldVisit from '../views/doctor/OldVisit.vue'

import routeAuth from "../utils/RoutingHelper.js"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  




  {
    path: '/edit-profile',
    name: 'editProfile',
    component: EditProfile,
    beforeEnter: routeAuth.isLoggedIn,
  },


  {
    path: '/add-visit',
    name: 'AddVisit',
    component: AddVisit,
    beforeEnter: routeAuth.isNurse,
  },
  {
    path: '/update-visit/:id/patient/:name',
    name: 'UpdateVisit',
    component: UpdateVisit,
    beforeEnter: routeAuth.isNurse,
  },





  {
    path: '/patients',
    name: 'patients',
    component: Patients,
    beforeEnter: routeAuth.isNurse,
  },
  {
    path: '/add-patient',
    name: 'addPatient',
    component: AddPatient,
    beforeEnter: routeAuth.isNurse,
  },
  {
    path: '/edit-patient/:id',
    name: 'editPatient',
    component: EditPatient,
    beforeEnter: routeAuth.isNurse,
  },
  {
    path: '/profile-patient/:id',
    name: 'nursePatientProfile',
    component: NursePatientProfile,
    beforeEnter: routeAuth.isNurse,
  },
  



  // doctor
  {
    path: '/patient-profile/:id',
    name: 'patientProfile',
    component: PatientProfile,
    // beforeEnter: routeAuth.isDoctor,
  },

  {
    path: '/patient-profile-medical/:id',
    name: 'patientProfileMedical',
    component: PatientProfileMedical,
    // beforeEnter: routeAuth.isDoctor,
  },
  
  {
    path: '/patient/:patientId/visit/:id',
    name: 'patientVisit',
    component: PatientVisit,
    // beforeEnter: routeAuth.isDoctor,
  },
  {
    path: '/patient/:patientId/old-visit/:id',
    name: 'patientOldVisit',
    component: PatientOldVisit,
    // beforeEnter: routeAuth.isDoctor,
  },
  
  




  {
    path: '*',
    name: 'NotFound',
    component: NotFound,
    beforeEnter: routeAuth.isLoggedIn,
  },
]

const router = new VueRouter({
  mode: 'history',
  routes,
  base: '/dental-clinic-local/dist/',
})

export default router
