<template>
  <div class="page-layout users">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>{{ langIsEng ? 'Edit profile' : ' ' }}</b></h3>
    </div>
    <div class="text-center text-danger my-2" v-show="isLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit"  v-show="!isLoading">
      <b-container class="bv-example-row">
        <b-row>
          <b-col lg="7" md="12">
            <b-form-group id="group-profile-edit-name" :label="langIsEng ? 'Name' : ''" label-for="profile-edit-name">
              <b-form-input
                id="profile-edit-name"
                name="profile-edit-name"
                v-model="$v.form.name.$model"
                :state="validateState('name')"
                aria-describedby="input-edit-name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-edit-name">
                {{langIsEng ? 'Name is required.' : ' '}}
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="7" md="12">
            <b-form-group id="group-profile-edit-user_name" :label="langIsEng ? 'Username - used to login' : '  -    '" label-for="profile-edit-user_name">
              <b-form-input
                id="profile-edit-user_name"
                name="profile-edit-user_name"
                v-model="$v.form.user_name.$model"
                :state="validateState('user_name')"
                aria-describedby="input-edit-user_name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-edit-user_name" v-if='!$v.form.user_name.required'>
                {{langIsEng ? 'Username is required.' : '  '}}
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="7" md="12">
            <b-form-group id="group-profile-edit-password" :label="langIsEng ? 'Password - used to login' : '  -    '" label-for="profile-edit-password">
              <b-form-input
                id="profile-edit-password"
                name="profile-edit-password"
                v-model="$v.form.password.$model"
                :state="validateState('password')"
                aria-describedby="input-edit-password"
                type="password"
              ></b-form-input>
              <b-form-invalid-feedback id="input-edit-password" v-if='!$v.form.password.minLength'>
                {{langIsEng ? 'Name is required.' : ''}}
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
        </b-row>
        <b-button type="submit" variant="primary">
          {{langIsEng ? 'Update' : ''}}
        </b-button>
      </b-container>
    </b-form>
  </div>
</template>

<script>
import { sendRequest }  from "../../../utils/RequestHelper.js"
import { validationMixin } from "vuelidate";
import store from "../../../store/index.js"
import { required} from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],
  data() {
    return {
      alertCountdown: 0,
      alertType: '',
      alertText: '',
      form: {
        name: null,
        user_name: null,
        password: null
      },
      adminMail: null,
      langIsEng: null,
    };
  },
  validations: {
    form: {
      name: {
        required
      },
      user_name: {
        required,
      },
      password: {
      }
    }
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    countDownChanged(alertCountdown) {
      this.alertCountdown = alertCountdown
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      this.isLoading = true;
      this.saveAdminData();
    },
    saveAdminData() {
      this.isLoading = true;
      if(this.form.password == null) 
        delete this.form.password
      var successCallback = () => {
        this.isLoading = false;
        this.alertType = "success"
        this.alertText = "   "
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        this.loadData();
      };
      var errorCallback = () => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "     "
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      };
      sendRequest('put', '/' + this.$store.getters.getRole + '/update-profile', true, this.form, successCallback, errorCallback);
    },
    loadData() {
      this.isLoading = true;
      var successCallback = (response) => {
        this.form = response.data.data;
        delete this.form.password;
        this.form.user_name;
        this.isLoading = false;
      };
      var errorCallback = () => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "     "
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      };
      sendRequest('get', '/'+ this.$store.getters.getRole + '/' + ( this.$store.getters.getRole == 'doctor' ? 'show-' : '' ) + 'profile', true, null, successCallback, errorCallback);
    }
  },
  created() {
    this.langIsEng = this.$store.getters.getlang == "en";
    this.isLoading = true;
    this.loadData();
  }
};
</script>