<template>
  <div class="page-layout users">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    
    <div class="text-center text-danger my-2" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit" v-show="!pageIsLoading">
      <div class="form-section">
        <div class="section-header" >
          <h4>
            <b>{{langIsEng ? "Visit information" : "   " + this.$route.params.name}}</b>
          </h4>
        </div>
        <b-row>
          <b-col lg="4" md="12">
            <b-form-group id="group-visit-add-doctor" :label="langIsEng ? 'Doctor' : ''" label-for="visit-add-doctor">
              <b-form-select
                id="visit-add-doctor"
                name="visit-add-doctor"
                v-model="$v.existPatientForm.doctor.$model"
                :options="doctors"
                :state="validateExistPatientState('doctor')"
                aria-describedby="input-add-doctor"
              ></b-form-select>
              <b-form-invalid-feedback id="input-add-doctor" v-if='!$v.existPatientForm.doctor.required'>
                {{langIsEng ? "Doctor is required." : " "}}
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="4" md="12">
            <label for="example-datepicker">{{langIsEng ? 'Visit date' : ' '}}</label>
            <b-form-datepicker class="date-textbox" id="visit-add-date"
              name="visit-add-date"
              v-model="$v.existPatientForm.date.$model"
              :state="validateExistPatientState('date')"
              aria-describedby="input-add-date"
              today-button reset-button close-button >
              </b-form-datepicker>
            <b-form-invalid-feedback id="input-add-date" v-if='!$v.existPatientForm.date.required'>
              {{langIsEng ? "Date is required." : "  "}}
            </b-form-invalid-feedback>
          </b-col>
          <b-col lg="4" md="12">
            <label for="example-datepicker">{{langIsEng ? 'Visit time' : ' '}}</label>
            <b-form-timepicker class="time-textbox" id="visit-add-time"
              name="visit-add-time"
              v-model="$v.existPatientForm.time.$model"
              :state="validateExistPatientState('time')"
              aria-describedby="input-add-time">
              </b-form-timepicker>
            <b-form-invalid-feedback id="input-add-time" v-if='!$v.existPatientForm.time.required'>
              {{langIsEng ? "Time is required." : "  "}}
            </b-form-invalid-feedback>
          </b-col>
        </b-row>
        <div>
          <b-button type="submit" variant="success">{{langIsEng ? 'Save Visit' : ''}}</b-button>
        </div>
        
      </div>
      
    </b-form>
  </div>
</template>

<script>
  import { sendRequest } from "../../../utils/RequestHelper.js"
  import { validationMixin } from "vuelidate";
  import { required, minLength, email} from "vuelidate/lib/validators";
  import store from "../../../store/index.js"

  export default {
    mixins: [validationMixin],
    data() {
      return {
        pageIsLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        doctors: [],
        doctorsDic: {},
        doctor: [],
        accounts: [],
        phone: null,
        existPatientForm: {
          doctor: null,
          date: null,
          time: null,
          patient_id: null
        },
        selectedPatient: null,
      };
    },
    validations: {
      existPatientForm: {
        patient_id: {
          required
        },
        doctor: {
          required
        },
        date: {
          required
        },
        time: {
          required
        },
      }
    },
    methods: {
      validateExistPatientState(name) {
        const { $dirty, $error } = this.$v.existPatientForm[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      resetForm() {
        this.existPatientForm = {
          patient_id: null,
          doctor: null,
          date: null,
          time: null
        };
        this.$nextTick(() => {
          this.$v.$reset();
        });
      },
      onSubmit() {
        this.$v.existPatientForm.$touch();
        if (this.$v.existPatientForm.$anyError) {
          return;
        }
        this.updateVisit();
      },
      
      updateVisit() {
        this.pageIsLoading = true;
        var successCallback = () => {
          this.pageIsLoading = false;
          this.alertType = "success"
          this.alertText = this.langIsEng ? "Visit added successfuly." : "    "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.resetForm();
        };
        var errorCallback = (error) => {
          var errorMessage = "     "
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = errorMessage
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/nurse/visits/' + this.$route.params.id, false, this.existPatientForm, successCallback, errorCallback);
      },
      getVisit(id) {
        this.pageIsLoading = true;
        var successCallback = (response) => {
          this.pageIsLoading = false;        
          this.existPatientForm = response.data.data;
          this.existPatientForm.doctor = {
            value: response.data.data.doctor_id,
            text: this.doctorsDic[response.data.data.doctor_id]
          }
        };
        var errorCallback = () => {
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/visits/' + id, true, null, successCallback, errorCallback);
      },
      getDoctors(){
        this.pageIsLoading = true;
        this.doctors = [];
        this.doctorsDic = {};
        var successCallback = (response) => {
          let doctors = response.data.data;
          doctors.forEach(doctor => {
            doctor.text = (this.langIsEng ? "Dr/ " : "/ " ) + doctor.name;
            doctor.value = doctor.id;
            this.doctorsDic[doctor.id] = doctor.text;
            this.doctors.push(doctor)
          });
          this.getVisit(this.$route.params.id)
        };
        var errorCallback = () => {
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "     "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/doctors', true, null, successCallback, errorCallback);
      },
    },
    created() {
      this.lang = store.getters.getlang;
      this.langIsEng = store.getters.getlang == "en";
      this.doctor = this.langIsEng ? this.accountStatusEn : this.accountStatusAr;
      if(this.$route.params.id)
        this.getDoctors(this.$route.params.id);
    }
  };
</script>
